.popup {
	width: 100%;
	height: 100vh;
	background-color: rgba($color-black, .8);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	opacity: 0;
	visibility: hidden;
	transition: all .3s;

	@support (-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px)) {
		-webkit-backdrop-filter: blur(10px);
		backdrop-filter: blur(10px);
		background-color: rgba($color-black, .3);
	}

	&__content {
		@include center-element;
		
		width: 75%;
		height: 50rem;
		background-color: $color-white;
		border-radius: 3px;
		overflow: hidden;
		display: table;
		opacity: 0;
		transform: translate(-50%, -50%) scale(.25);
		transition: all .4s;
	}

	&__left {
		width: 33.33333%;
		display: table-cell;
	}

	&__right {
		width: 66.66667%;
		display: table-cell;
		vertical-align: middle;
		padding: 3rem 5rem;
	}

	&__img {
		display: block;
		width: 100%;
	}

	&__text {
		font-size: 1.4rem;
		margin-bottom: 4rem;

		-moz-column-count: 2;
		-moz-column-gap: 4rem;
		-moz-column-rule: 1px solid $color-grey-light-2;

		column-count: 2;
		column-gap: 4rem;
		column-rule: 1px solid $color-grey-light-2;

		-moz-hyphens: auto;
		-webkit-hyphens: auto;
		-ms-hyphens: auto;
			hyphens: auto;
	}

	&:target {
		opacity: 1;
		visibility: visible;
	}

	&:target &__content {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}

	&__close {
		color: $dark-grey;
		text-decoration: none;
		position: absolute;
		top: 2.5rem;
		right: 2.5rem;
		font-size: 3rem;
		display: inline-block;
		transition: all .2s;
		line-height: 1;

		&:hover {
			color: $primary-color;
		}
	}
}