.form {
	&__group:not(:last-child) {
		margin-bottom: 2rem;
	}

	&__input {
		font-size: 1.5rem;
		font-family: inherit;
		color: inherit;
		padding: 1.5rem 2rem;
		border-radius: 3px;
		border: none;
		border-bottom: 3px solid transparent;
		display: block;
		width: 90%;
    	transition: all .3s;

    	@include media-breakpoint(tablet) {
			width: 100%;
		}

		&:focus {
			outline: none;
			box-shadow: 0 1rem 2rem rgba($color-black, .1);
			border-bottom-color: $primary-color-dark;

			&:invalid {
				border-bottom-color: $secondary-color-dark;
			}
		}

		&::-webkit-input-placeholder {
			color: $dark-grey-2;
		}

		@at-root {
			&:placeholder-shown + .form__label {
				opacity: 0;
				visibility: hidden;
				transform: translateY(-4rem);
			}
		}
	}

	&__label {
		font-size: 1.2rem;
		font-weight: 700;
		margin-left: 2rem;
		margin-top: .7rem;
		display: block;
		transition: all .3s;
	}

	&__radio-group {
		width: 49%;
		display: inline-block;

		@include media-breakpoint(tablet) {
			width: 100%;
			margin-bottom: 1rem;
		}
	}

	&__radio-input {
		display: none;
	}

	&__radio-label {
		font-size: $default-font-size;
		cursor: pointer;
		position: relative;
		padding-left: 4.5rem;
	}

	&__radio-button {
		width: 3rem;
		height: 3rem;
		border: 5px solid $primary-color;
		border-radius: 50%;
		display: inline-block;
		position: absolute;
		top: -.4rem;
		left: 0;
		
		&::after {
			content: "";
			display: block;
			width: 1rem;
			height: 1rem;
			border-radius: 50%;
			background-color: $primary-color;
			@include center-element;
			opacity: 0;
			transition: all .2s;

			@at-root {
				.form__radio-input:checked + .form__radio-label  & {
					opacity: 1;
				}
			}
		}
	}
}