.card {
    perspective: 150rem;
    -moz-persfective: 150rem;
    position: relative;
    height: 52rem;

    &__side {
        height: 52rem;
        transition: all .8s ease;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
        border-radius: 3px;
        overflow: hidden;
        box-shadow: 0 1.5rem 4rem rgba($color-black, .15);

        &--front {
            background-color: $color-white;
        }

        &--back {
            transform: rotateY(180deg);

            &-1 {
                @include background-gradient(to bottom right, $secondary-color, $secondary-color-dark);
            }

            &-2 {
                @include background-gradient(to bottom right, $primary-color, $primary-color-dark);
            }

            &-3 {
                @include background-gradient(to bottom right, $tertiary-color, $tertiary-color-dark);
            }
        }
    }

    &:hover &__side--front {
        transform: rotateY(-180deg);
    }

    &:hover &__side--back {
        transform: rotateY(0);
    }

    &__picture {
        background-size: cover;
        height: 23rem;
        background-blend-mode: screen;
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
                clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;

        &--1 {
            @include background-gradient(to bottom right, $secondary-color, $secondary-color-dark, '../img/nat-5.jpg');
        }

        &--2 {
            @include background-gradient(to bottom right, $primary-color, $primary-color-dark, '../img/nat-6.jpg');
        }

        &--3 {
            @include background-gradient(to bottom right, $tertiary-color, $tertiary-color-dark, '../img/nat-7.jpg');
        }
    }

    &__heading {
        font-size: 2.8rem;
        font-weight: 300;
        text-transform: uppercase;
        text-align: right;
        color: $color-white;
        position: absolute;
        top: 12rem;
        right: 2rem;
        width: 75%;
        z-index: 1;
    }

    &__heading-span {
        padding: 1rem 1.5rem;
        -webkit-box-decoration-break: clone;
                box-decoration-break: clone;

        &--1 {
            @include background-gradient(to right bottom, rgba($secondary-color, .85), rgba($secondary-color-dark, .85));
        }

        &--2 {
            @include background-gradient(to right bottom, rgba($primary-color, .85), rgba($primary-color-dark, .85));
        }

        &--3 {
            @include background-gradient(to right bottom, rgba($tertiary-color, .85), rgba($tertiary-color-dark, .85));
        }
    }

    &__details {
        padding: 3rem;

        ul {
            list-style: none;
            width: 80%;
            margin: 0 auto;

            li {
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;

                &:not(:last-child) {
                    border-bottom: 1px solid $color-grey-light-2;
                }
            }
        }

    }

    &__cta {
        @include center-element;
        text-align: center;
        width: 90%;
    }

    &__price-box {
        text-align: center;
        color: $color-white;
        margin-bottom: 8rem;
    }

    &__price-only {
        font-size: 1.4rem;
        text-transform: uppercase;
    }

    &__price-value {
        font-size: 6rem;
        font-weight: 100;
    }

    //@include media-breakpoint(tablet) {
    @media only screen and (max-width: 56.25em), only screen and (hover: none) {
        height: auto;
        background-color: $color-white;
        box-shadow: 0 1.5rem 4rem rgba($color-black, .15);

        &__side {
            height: auto;
            position: relative;
            box-shadow: none;

            &--back {
                transform: rotateY(0);
                clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
            }
        }

        &:hover &__side--front {
            transform: rotateY(0);
        }

        &__details {
            padding: 1rem 3rem;
        }

        &__cta {
            position: relative;
            top: 0;
            left: 0;
            transform: translate(0);
            width: 100%;
            padding: 7rem 4rem 4rem 4rem;
        }

        &__price-box {
            margin-bottom: 3rem;
        }

        &__price-value {
            font-size: 4rem;
        }
    }
}
