.footer {
	background-color: $dark-grey-3;
	padding: 10rem 0;
	font-size: 1.4rem;

	@include media-breakpoint(tablet) {
		padding: 8rem 0;
	}

	&__logo-box {
		text-align: center;
		margin-bottom: 8rem;

		@include media-breakpoint(tablet) {
			margin-bottom: 6rem;
		}
	}

	&__logo {
		width: 15rem;
		height: auto;
	}

	&__navigation {
		border-top: 1px solid $dark-grey;
		padding-top: 2rem;
		display: inline-block;

		@include media-breakpoint(tablet) {
			width: 100%;
			text-align: center;
		}
	}

	&__list {
		letter-spacing: normal;
	}

	&__item {
		display: inline-block;

		&:not(last-child) {
			margin-right: 1.5rem;
		}
	}

	&__link {
		&:link,
		&:visited {
			color: $color-grey-light-1;
			background-color: $dark-grey-3;
			text-decoration: none;
			text-transform: uppercase;
			display: inline-block;
			transition: all .2s;
		}

		&:hover,
		&:active {
			color: $primary-color;
			box-shadow: 0 1rem 2rem rgba($color-black, .4);
			transform: rotate(5deg) scale(1.3);
		}
	}

	&__copyright {
		@extend .footer__navigation;
		width: 85%;
		float: right;

		@include media-breakpoint(tablet) {
			width: 100%;
			float: none;
		}
	}
}